export default {
	props: {
		score: 0,
		showNum: 0
	},
	data() {
		return {
			yellowStarNum: 0,
			grayStarNum: 5
		}
	},
	created() {
		let tempScore = 0
		if (this.score !== null && this.score !== undefined && this.score !== '') {
			tempScore = this.score
		}
		
		// 星星颜色个数
		this.yellowStarNum = parseInt(tempScore/2)
		this.grayStarNum = 5- this.yellowStarNum
	}
}
